import React from 'react';
import Options from '../Optionis/Options';
import './Ans.css'
import { EyeIcon } from '@heroicons/react/24/solid'



const Ans = ({ ans, id, questions }) => {
    const option = ans.options
    console.log(option.length)

    const correctAns = ans.correctAnswer
    console.log(correctAns)
    const Eye = () => {
        if (true) {
            alert(correctAns)
        }
    }
    return (
        <div>


            <span><p>{ans.question} <span><EyeIcon onClick={() => Eye()} className="width " /></span></p></span>


            <div className='option-container'>
                {option.map(O => <Options correctAns={correctAns} option={O}></Options>)}
            </div>
        </div>
    );
};

export default Ans;