import React from 'react';
import { useLoaderData } from 'react-router-dom';
import Ans from '../Ans/Ans';
import './DetailCodeCart.css'

const DetailCodeCart = () => {
    const DetailsDatas = useLoaderData().data
    const { id, name, questions, options } = DetailsDatas


    console.log(DetailsDatas)
    return (
        <div className='container'>
            <h2>Quiz of {name}</h2>
            {
                questions.map(q => <Ans questions={questions} id={id} ans={q}></Ans>)
            }


        </div>
    );
};

export default DetailCodeCart;
