import React from 'react';
import { useLoaderData } from 'react-router-dom';
import CodeCarts from '../CodeCarts/CodeCarts';
import './Home.css'

const Home = () => {
    const codeDatas = useLoaderData().data;
    // const StartPractice = (cartId) => {
    //     console.log(cartId)
    // }


    return (
        <div className='single-cart'>
            {
                codeDatas.map(codeData => <CodeCarts
                    key={codeData.id}
                    codeData={codeData}
                // StartPractice={StartPractice}
                ></CodeCarts>)
            }
        </div>
    );
};

export default Home;