import logo from './logo.svg';
import './App.css';
import { createBrowserRouter, RouterProvider } from 'react-router-dom';
import Header from './Componets/Header/Header';
import Main from './Layout/Main';
import Home from './Componets/Home/Home';
import Statictics from './Componets/Statictics/Statictics';
import Blog from './Componets/Blog/Blog';
import DetailCodeCart from './Componets/DetailCodeCart/DetailCodeCart';

function App() {
  const router = createBrowserRouter([
    {
      path: '/', element: <Main></Main>, children: [
        {
          path: '/home',
          loader: async () => {
            return fetch('https://openapi.programming-hero.com/api/quiz')
          },
          element: <Home></Home>
        },
        {
          path: '/home/:homeId',
          loader: async ({ params }) => {
            return fetch(`https://openapi.programming-hero.com/api/quiz/${params.homeId}`)
          },
          element: <DetailCodeCart></DetailCodeCart>
        },
        { path: '/statictis', element: <Statictics></Statictics> },
        { path: '/blog', element: <Blog></Blog> }
      ]
    },

  ])
  return (
    <div className="App">

      <RouterProvider router={router}></RouterProvider>
    </div>
  );
}

export default App;
