import React from 'react';
import { Link } from 'react-router-dom';
import './CodeCarts.css'
const CodeCarts = ({ codeData, StartPractice }) => {
    const { id, logo, name } = codeData;

    return (
        <div className='Carts-container'>

            <img src={logo} alt="" />
            <div>

                <h5>{name}</h5>
                {/* <button onClick={() => StartPractice(codeData)}>Start Practice</button> */}
                <Link to={`/home/${id}`}><button className='btn'>Start Practice</button></Link>

            </div>
        </div>
    );
};

export default CodeCarts;