import React from 'react';
import './Options.css'

const Options = ({ option, correctAns }) => {
    const correct = (id) => {
        if (id === correctAns) {
            alert('Your  ans  is  TRUE')
        } else {
            alert('Your ans is False')
        }
    }
    return (

        <div className='r' >


            <p onClick={() => correct(option)}>  <span><input type='radio' value='option' name='option' /></span>{option}</p>


        </div>
    );
};
export default Options;